.hero {
  background: #f1f1f1 url("../img/main-bg.jpg") no-repeat center top;
  background-size: cover;
  padding-top: 120px;
  padding-bottom: 270px;
  margin-top: 80px;
  &__title {
    margin-bottom: 30px;
    font-size: 30px;
  }
  &__subtitle {
    display: block;
    margin-bottom: 67px;
    font-size: 30px;
    @include bold;
  }
}

@media (max-width: 991px) {
  .hero {
    background-size: 100%;
    background-position: top right;
  }
}
